/**
 * Settings Page - User settings and preferences
 *
 * ✅ CODE-003: Migrated to TypeScript
 */

import React, { useState, type FC } from 'react';
import { toast } from 'sonner';
import { useAuthStore } from '../store/useAuthStore';

// ===========================
// Types
// ===========================

interface Settings {
  emailNotifications: boolean;
  autoAnalysis: boolean;
  darkMode: boolean;
  language: string;
}

// ===========================
// Component
// ===========================

const SettingsPage: FC = () => {
  const { user } = useAuthStore();

  const [settings, setSettings] = useState<Settings>({
    emailNotifications: true,
    autoAnalysis: false,
    darkMode: true,
    language: 'he',
  });

  const handleSettingChange = (key: keyof Settings, value: boolean | string): void => {
    setSettings((prev) => ({ ...prev, [key]: value }));
    toast.success('ההגדרות נשמרו בהצלחה');
  };

  return (
    <div className="p-8">
      <h1 className="text-4xl font-bold text-white mb-8">הגדרות</h1>

      <div className="max-w-3xl space-y-6">
        <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-6 border border-white/20">
          <h2 className="text-2xl font-bold text-white mb-4">פרטי חשבון</h2>
          <div className="space-y-4">
            <div>
              <label className="block text-blue-200 text-sm mb-2">שם מלא</label>
              <input
                type="text"
                value={user?.name || 'משתמש'}
                disabled
                className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white"
              />
            </div>
            <div>
              <label className="block text-blue-200 text-sm mb-2">אימייל</label>
              <input
                type="email"
                value={user?.email || ''}
                disabled
                className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white"
              />
            </div>
          </div>
        </div>

        <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-6 border border-white/20">
          <h2 className="text-2xl font-bold text-white mb-4">הגדרות כלליות</h2>
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-white font-semibold">התראות במייל</p>
                <p className="text-blue-300 text-sm">קבל עדכונים על ניתוחים חדשים</p>
              </div>
              <button
                onClick={() => handleSettingChange('emailNotifications', !settings.emailNotifications)}
                className={`w-12 h-6 rounded-full transition-colors ${
                  settings.emailNotifications ? 'bg-green-500' : 'bg-gray-600'
                }`}
              >
                <div
                  className={`w-5 h-5 bg-white rounded-full transition-transform ${
                    settings.emailNotifications ? 'translate-x-6' : 'translate-x-1'
                  }`}
                />
              </button>
            </div>

            <div className="flex items-center justify-between">
              <div>
                <p className="text-white font-semibold">ניתוח אוטומטי</p>
                <p className="text-blue-300 text-sm">התחל ניתוח מיד אחרי העלאה</p>
              </div>
              <button
                onClick={() => handleSettingChange('autoAnalysis', !settings.autoAnalysis)}
                className={`w-12 h-6 rounded-full transition-colors ${
                  settings.autoAnalysis ? 'bg-green-500' : 'bg-gray-600'
                }`}
              >
                <div
                  className={`w-5 h-5 bg-white rounded-full transition-transform ${
                    settings.autoAnalysis ? 'translate-x-6' : 'translate-x-1'
                  }`}
                />
              </button>
            </div>
          </div>
        </div>

        <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-6 border border-white/20">
          <h2 className="text-2xl font-bold text-white mb-4">מפתחות API</h2>
          <p className="text-blue-300 mb-4">ניהול מפתחות לשירותי AI</p>
          <button className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors">
            נהל מפתחות
          </button>
        </div>
      </div>
    </div>
  );
};

export default SettingsPage;
